export default {
  data: function data() {
    return {
      loading: false,
      tableHeight: 800,
      reSizeTime: 0,
      pageSizes: [12, 24, 48],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_productsPerPage") ? Number(localStorage.getItem("c_productsPerPage")) : 12,
      items: [],
      total: 0,
      totalPage: 0,
      filterParams: {
        name: "",
        relationshipId: "",
        priceFrom: undefined,
        priceTo: undefined,
        accountId: ""
      },
      filterParamsDefault: "{}",
      selectLoading: false,
      selectArr: [],
      selectLoading2: false,
      selectArr2: [],
      updateProductLoading: false,
      cnow: 0,
      now: Date.parse(new Date()) / 1000 - this.$store.state.userInfo.timeDiff,
      updateTime: localStorage.getItem('c_products-updateTime') ? Number(localStorage.getItem('c_products-updateTime')) : 0
    };
  },
  watch: {
    $route: "gotoPage"
  },
  mounted: function mounted() {
    var _this = this;

    this.cnow = setInterval(function () {
      _this.now = Date.parse(new Date()) / 1000 - _this.$store.state.userInfo.timeDiff;
    }, 1000);
    this.filterParamsDefault = JSON.stringify(this.filterParams);
    this.getItem();

    window.onresize = function () {
      _this.$getTableHeight(_this);
    };
  },
  beforeDestroy: function beforeDestroy() {
    window.onresize = function () {};
  },
  methods: {
    formatminute: function formatminute(m) {
      return moment.duration(m / 60, "minutes").locale("en").humanize();
    },
    updateProducts: function updateProducts() {
      var _this2 = this;

      this.updateProductLoading = true; // this.$Burying()

      this.$apiCall('api.ShopifyAccount.getProductByHand', {}, function (r) {
        _this2.updateProductLoading = false;

        if (r.ErrorCode == "9999") {
          _this2.updateTime = r.Data.Results;
          localStorage.setItem('c_products-updateTime', r.Data.Results);

          _this2.$elementMessage('Update Product Success', 'success');
        } else {
          _this2.$elementMessage(r.Message, 'error');
        }
      });
    },
    delP: function delP(item) {
      var _this3 = this;

      this.$confirm("Are you sure you want to delete it?", "", {
        confirmButtonText: "Delete",
        cancelButtonText: "Cancel",
        type: "error"
      }).then(function () {
        _this3.$apiCall("api.Product.deleteByUser", {
          id: item.id
        }, function (r) {
          if (r.ErrorCode == 9999) {
            _this3.$message({
              message: "deleted successfully",
              type: "success"
            });

            _this3.getItem(true);
          } else {
            _this3.$elementMessage(r.Message, "error");
          }
        });
      }).catch(function () {});
    },
    remoteMethodStores: function remoteMethodStores(query) {
      var _this4 = this;

      //获取筛选店铺名
      this.selectArr2 = [];
      if (!query) return;
      this.selectLoading2 = true;
      this.$apiCall("api.Invoice.findStores", {
        shopName: query
      }, function (r) {
        _this4.selectLoading2 = false;

        if (r.ErrorCode == 9999) {
          _this4.selectArr2 = r.Data.Results;
        } else {
          _this4.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    remoteMethodVendor: function remoteMethodVendor(query) {
      var _this5 = this;

      //获取筛选客户
      this.selectArr = [];
      if (!query) return;
      this.selectLoading = true;
      this.$apiCall("api.Relationship.findByCustomer", {
        name: query
      }, function (r) {
        _this5.selectLoading = false;

        if (r.ErrorCode == 9999) {
          _this5.selectArr = r.Data.Results;
        } else {
          _this5.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    getItem: function getItem(s) {
      var _this6 = this;

      this.loading = true;
      this.$apiCall("api.Product.findByUser", {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        name: this.filterParams.name,
        relationshipId: this.filterParams.relationshipId,
        priceFrom: this.filterParams.priceFrom,
        priceTo: this.filterParams.priceTo,
        accountId: this.filterParams.accountId
      }, function (r) {
        _this6.loading = false;

        if (r.ErrorCode == "9999") {
          _this6.items = [];
          r.Data.Results.forEach(function (e) {
            e.imgUrl = JSON.parse(e.imgsJson).length ? JSON.parse(e.imgsJson)[0] : "";
            e.adminUrl = e.adminUrl.replace(/\\/g, "");
            e.productUrl = e.productUrl.replace(/\\/g, "");

            _this6.items.push(e);
          });
          _this6.total = Number(r.Data.Pagination.totalCount);
          _this6.totalPage = Number(r.Data.Pagination.totalPage);

          _this6.$getTableHeight(_this6);

          if (!s) _this6.$refs.gridTable.$el.scrollTop = 0;
        } else {
          _this6.$elementMessage(r.Message, "error");
        }
      });
    },
    clearFilter: function clearFilter() {
      //清空筛选
      // this.$Burying()
      this.filterParams = JSON.parse(this.filterParamsDefault);
      this.selectLoading = false;
      this.selectArr = [];
      this.selectLoading2 = false;
      this.selectArr2 = [];

      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    filterGetItem: function filterGetItem() {
      //筛选
      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_productsPerPage", val);
      this.getItem();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? this.$route.query.page : 1;
      this.getItem();
    }
  }
};